import * as React from "react";
import ChartSoldeFinancier from "./ChartSoldeFinancier";
import ChartNiveauVie from "./ChartNiveauVie";
import ChartTempsRetraite from "./ChartTempsRetraite";
import ReglageParametres from "./ReglageParametres";
import Button from "@mui/material/Button";
import Fade from "@mui/material/Fade";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Stack from "@mui/material/Stack";
import Slider from "@mui/material/Slider";
import TextField from "@mui/material/TextField";
import InputAdornment from "@mui/material/InputAdornment";
import { Info } from "@mui/icons-material";
import Box from "@mui/material/Box";
import calculerSolde from "../libs/calculerSolde.js";
import calculerSoldeTouteAnnee from "../libs/calculerSoldeTouteAnnee.js";
import quandEquilibre from "../libs/quandEquilibre.js";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Filter1OutlinedIcon from "@mui/icons-material/Filter1Outlined";
import Filter2OutlinedIcon from "@mui/icons-material/Filter2Outlined";
import Filter3OutlinedIcon from "@mui/icons-material/Filter3Outlined";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import RemoveCircleIcon from "@mui/icons-material/RemoveCircle";
import ShowChartIcon from "@mui/icons-material/ShowChart";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepContent from "@mui/material/StepContent";
import StepLabel from "@mui/material/StepLabel";
import StepButton from "@mui/material/StepButton";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import PhraseCourbeRouge from "./PhraseCourbeRouge";
import ContenuEtape from "./ContenuEtape";
let sizeH1 = "45px";
let sizeInfo = 12;

const titreEtape = [
  "Retrouvez l'quilibre financier",
  "Sauvegardez le niveau de vie des retraits",
  "Veillez  la dure de la retraite",
];

const messageExplicationProjection = [
  "Le Conseil d'orientation des retraites (COR) projette que le systme actuel des rformes des retraites ne sera pas durablement  l'quilibre avant 2054. ",
  "Le niveau de vie des personnes  la retraite par rapport  l'ensemble de la population devrait tre plus faible dans les dcennies  venir, selon les projections du COR. Cela ne signifie pas que ce niveau de vie sera moindre dans l'absolu, mais uniquement compar  celui du reste de la population et en particulier des actifs qui a tendance  augmenter plus vite. ",
  "En 2010, le gouvernement de Nicolas Sarkozy avance l'ge du dpart lgal de deux ans, ce qui a fait reculer significativement la dure de retraite de ceux partis ds 2010, que l'augmentation de l'esprance de vie ne compensera pas avant des dizaines d'annes. ",
];

const messageExplicationAction = [
  "A votre tour de modifier les paramtres de votre rforme et voir l'impact que cela aurait sur l'quilibre financier.",
  "Vous pouvez jouer avec les diffrents paramtres de la rforme pour essayer de modrer cette diffrence de niveau de vie entre les retraits et le reste de la population.",
  "En agissant sur l'ge de dpart, vous pourrez voir l'volution de la dure de la retraite prvisionnelle, en prenant en compte l'volution de l'esprance de vie que projette l'Insee.",
];

function App() {
  const baseAgeDepart = 62;
  const basePensionMoyenne = 1644;
  const baseNiveauCotisation = 30.5;

  const [contexteSocioEco, setContexteSocioEco] = React.useState("Neutre");
  const [niveauCotisationUtilisateur, setNiveauCotisationUtilisateur] =
    React.useState(baseNiveauCotisation);
  const [parametresModifies, setParametresModifies] = React.useState(0);
  const [ageDepart, setAgeDepart] = React.useState(baseAgeDepart);
  const [pensionMoyenne, setPensionMoyenne] =
    React.useState(basePensionMoyenne);
  const [activeStep, setActiveStep] = React.useState(0);
  const [skipped, setSkipped] = React.useState(new Set());

  const isStepOptional = (step) => {
    return false;
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  function handleChangeStep(stepTarget) {
    setActiveStep(stepTarget);
  }

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleSkip = () => {
    if (!isStepOptional(activeStep)) {
      throw new Error("You can't skip a step that isn't optional.");
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped((prevSkipped) => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const handleReset = () => {
    setActiveStep(0);
    setAgeDepart(baseAgeDepart);
    setPensionMoyenne(basePensionMoyenne);
    setNiveauCotisationUtilisateur(baseNiveauCotisation);
    setParametresModifies(0);
  };

  const handleContexteSocioEco = (event, newContexteSocioEco) => {
    setContexteSocioEco(newContexteSocioEco);
  };
  const handleAgeDepart = (event) => {
    setAgeDepart(event.target.value);
    setParametresModifies(1);
    if (
      event.target.value == baseAgeDepart &&
      pensionMoyenne == basePensionMoyenne &&
      niveauCotisationUtilisateur == baseNiveauCotisation
    ) {
      setParametresModifies(0);
    }
  };

  const handleNiveauCotisationUtilisateur = (event) => {
    setNiveauCotisationUtilisateur(event.target.value);
    setParametresModifies(1);
    if (
      event.target.value == baseNiveauCotisation &&
      pensionMoyenne == basePensionMoyenne &&
      ageDepart == baseAgeDepart
    ) {
      setParametresModifies(0);
    }
  };

  const handlePensionMoyenne = (event) => {
    setPensionMoyenne(event.target.value);
    setParametresModifies(1);
    if (
      event.target.value == basePensionMoyenne &&
      niveauCotisationUtilisateur == baseNiveauCotisation &&
      ageDepart == baseAgeDepart
    ) {
      setParametresModifies(0);
    }
  };

  let texteEquilibreFinancier =
    " l'quilibre ds " +
    quandEquilibre(
      contexteSocioEco,
      niveauCotisationUtilisateur / 100,
      ageDepart,
      pensionMoyenne,
      baseAgeDepart,
      baseNiveauCotisation,
      basePensionMoyenne
    );
  if (
    calculerSolde(
      0,
      2070,
      contexteSocioEco,
      0,
      niveauCotisationUtilisateur / 100,
      ageDepart,
      pensionMoyenne
    ) < 0
  ) {
    texteEquilibreFinancier = "en dficit";
  }

  let texteNiveauVie = "plus bas";
  if (
    calculerSolde(
      1,
      2070,
      contexteSocioEco,
      0,
      niveauCotisationUtilisateur / 100,
      ageDepart,
      pensionMoyenne
    ) -
      calculerSolde(
        1,
        2070,
        contexteSocioEco,
        1,
        niveauCotisationUtilisateur / 100,
        ageDepart,
        pensionMoyenne
      ) >
    0
  ) {
    texteNiveauVie = "plus lev";
  }

  let texteDureeRetraite = "moins de temps";
  if (
    calculerSolde(
      2,
      2070,
      contexteSocioEco,
      0,
      niveauCotisationUtilisateur / 100,
      ageDepart,
      pensionMoyenne
    ) -
      calculerSolde(
        2,
        2022,
        contexteSocioEco,
        0,
        niveauCotisationUtilisateur / 100,
        ageDepart,
        pensionMoyenne
      ) >
    0
  ) {
    texteDureeRetraite = "plus de temps";
  }
  if (
    calculerSolde(
      2,
      2070,
      contexteSocioEco,
      0,
      niveauCotisationUtilisateur / 100,
      ageDepart,
      pensionMoyenne
    ) -
      calculerSolde(
        2,
        2022,
        contexteSocioEco,
        0,
        niveauCotisationUtilisateur / 100,
        ageDepart,
        pensionMoyenne
      ) ==
    0
  ) {
    texteDureeRetraite = "identique";
  }

  const theme = createTheme({
    typography: {
      fontFamily: "Franklin Gothic",
    },
    palette: {
      primary: {
        main: "#ab2926",
      },
      info: {
        main: "#959da2",
      },
    },
  });

  return (
    <ThemeProvider theme={theme}>
      <div className="">
        <Box sx={{ maxWidth: "800px" }}>
          <Stack spacing={4}>
            <Typography variant="h1" fontSize={sizeH1} id="debutEtape">
              Simulez votre propre rforme des retraites
              <Typography variant="h1" fontSize={sizeH1} color="primary">
                et observez son impact jusqu'en 2070
              </Typography>
            </Typography>

            <div>
              <Box
                sx={{
                  marginBottom: "40px",
                  paddingLeft: "5px",
                  paddingRight: "5px",
                }}
              >
                <ContenuEtape
                  baseAgeDepart={baseAgeDepart}
                  basePensionMoyenne={basePensionMoyenne}
                  baseNiveauCotisation={baseNiveauCotisation}
                  contexteSocioEco={contexteSocioEco}
                  niveauCotisationUtilisateur={niveauCotisationUtilisateur}
                  parametresModifies={parametresModifies}
                  ageDepart={ageDepart}
                  pensionMoyenne={pensionMoyenne}
                  activeStep={0}
                  _activeStep={titreEtape[0]}
                  __activeStep={messageExplicationProjection[0]}
                  ___activeStep={messageExplicationAction[0]}
                  isStepOptional={isStepOptional}
                  handleNext={handleNext}
                  handleBack={handleBack}
                  handleSkip={handleSkip}
                  handleReset={handleReset}
                  handleAgeDepart={handleAgeDepart}
                  handleNiveauCotisationUtilisateur={
                    handleNiveauCotisationUtilisateur
                  }
                  handlePensionMoyenne={handlePensionMoyenne}
                  texteEquilibreFinancier={texteEquilibreFinancier}
                  texteNiveauVie={texteNiveauVie}
                  texteDureeRetraite={texteDureeRetraite}
                ></ContenuEtape>
              </Box>
              <Box
                sx={{
                  marginBottom: "40px",
                  paddingLeft: "5px",
                  paddingRight: "5px",
                }}
              >
                <ContenuEtape
                  baseAgeDepart={baseAgeDepart}
                  basePensionMoyenne={basePensionMoyenne}
                  baseNiveauCotisation={baseNiveauCotisation}
                  contexteSocioEco={contexteSocioEco}
                  niveauCotisationUtilisateur={niveauCotisationUtilisateur}
                  parametresModifies={parametresModifies}
                  ageDepart={ageDepart}
                  pensionMoyenne={pensionMoyenne}
                  activeStep={1}
                  _activeStep={titreEtape[1]}
                  __activeStep={messageExplicationProjection[1]}
                  ___activeStep={messageExplicationAction[1]}
                  isStepOptional={isStepOptional}
                  handleNext={handleNext}
                  handleBack={handleBack}
                  handleSkip={handleSkip}
                  handleReset={handleReset}
                  handleAgeDepart={handleAgeDepart}
                  handleNiveauCotisationUtilisateur={
                    handleNiveauCotisationUtilisateur
                  }
                  handlePensionMoyenne={handlePensionMoyenne}
                  texteEquilibreFinancier={texteEquilibreFinancier}
                  texteNiveauVie={texteNiveauVie}
                  texteDureeRetraite={texteDureeRetraite}
                ></ContenuEtape>
              </Box>
              <Box sx={{ paddingLeft: "5px", paddingRight: "5px" }}>
                <ContenuEtape
                  baseAgeDepart={baseAgeDepart}
                  basePensionMoyenne={basePensionMoyenne}
                  baseNiveauCotisation={baseNiveauCotisation}
                  contexteSocioEco={contexteSocioEco}
                  niveauCotisationUtilisateur={niveauCotisationUtilisateur}
                  parametresModifies={parametresModifies}
                  ageDepart={ageDepart}
                  pensionMoyenne={pensionMoyenne}
                  activeStep={2}
                  _activeStep={titreEtape[2]}
                  __activeStep={messageExplicationProjection[2]}
                  ___activeStep={messageExplicationAction[2]}
                  isStepOptional={isStepOptional}
                  handleNext={handleNext}
                  handleBack={handleBack}
                  handleSkip={handleSkip}
                  handleReset={handleReset}
                  handleAgeDepart={handleAgeDepart}
                  handleNiveauCotisationUtilisateur={
                    handleNiveauCotisationUtilisateur
                  }
                  handlePensionMoyenne={handlePensionMoyenne}
                  texteEquilibreFinancier={texteEquilibreFinancier}
                  texteNiveauVie={texteNiveauVie}
                  texteDureeRetraite={texteDureeRetraite}
                ></ContenuEtape>
              </Box>
            </div>
            <Button onClick={handleReset} href="#debutEtape">Recommencer</Button>
          </Stack>
        </Box>
      </div>
    </ThemeProvider>
  );
}

export default App;
