import * as React from "react";
import Typography from "@mui/material/Typography";
import ShowChartIcon from "@mui/icons-material/ShowChart";

const PhraseCourbeRouge = ({
  step,
  texteEquilibreFinancier,
  texteNiveauVie,
  texteDureeRetraite,
}) => {
  if (step === 0) {
    return (
      <Typography>
        Comme le montre{" "}
        <Typography variant="span" color="primary" fontWeight="bold">
          <ShowChartIcon /> la courbe rouge
        </Typography>
        , votre rforme pourrait amener les caisses de retraite  tre{" "}
        <Typography variant="span" color="primary" fontWeight="bold">
          {texteEquilibreFinancier}
        </Typography>
        .{" "}
      </Typography>
    );
  }
  if (step === 1) {
    return (
      <Typography>
        Comme le montre{" "}
        <Typography variant="span" color="primary" fontWeight="bold">
          <ShowChartIcon /> la courbe rouge
        </Typography>
        , votre rforme amnerait le niveau de vie des retraits par rapport au
        reste de la population active  un niveau{" "}
        <Typography variant="span" color="primary" fontWeight="bold">
          {texteNiveauVie}
        </Typography>{" "}
        qu'en l'absence de rforme, selon les projections du COR.{" "}
      </Typography>
    );
  }
  if (step === 2) {
    return (
      <Typography>
        Comme le montre{" "}
        <Typography variant="span" color="primary" fontWeight="bold">
          <ShowChartIcon /> la courbe rouge
        </Typography>
        , votre rforme aboutirait  ce que les retraits qui partiront  la
        retraite en 2070 passeront {" "}
        <Typography variant="span" color="primary" fontWeight="bold">
          {texteDureeRetraite}
        </Typography>{" "}
         la retraite que les retraits partis en 2022.{" "}
      </Typography>
    );
  }
};

export default PhraseCourbeRouge;
